.content_wrap {
    max-width : 1000px;
    width: 90%;
    margin : 0 auto;
    padding : 60px 0;
    color: white;
    font-weight: 600;
}

footer {
    text-align: center;
}

footer h3{
    font-family: 'Open Sans', cursive;
}

footer img{
    background-color: white;
    border-radius: 8px;
    padding: 4px;
    width: 20px;
    height: 20px;
}

.contact_list {
    list-style-type: none;
    padding:0;
    display: flex;
    justify-content: center;
}

.contact_list a {
    margin-bottom: 4px;
    padding: 15px;
    text-decoration: none;
}
